import React, { useState } from "react";
import Select from "react-select";
import { useProductsActions } from "../prpductProvider/ProductProvider";
import "../Filter/filter.css";
import SearchBar from "../../common/search/Search";
const options = [
  { value: "", label: "ALL" },
  { value: "M", label: "M" },
  { value: "L", label: "L" },
  { value: "XL", label: "XL" },
  { value: "XXL", label: "XXL" },
  { value: "XXXL", label: "XXXL" },
];

const priceOptions = [
  { sort: "", label: "random" },
  { sort: "Cheapest", label: "Cheapest" },
  { sort: "Most Expensive", label: "Most Expensive" },
];
const Filter = () => {
  const dispatc = useProductsActions();

  const [value, setValue] = useState("");
  const [priceStart, setPriceStart] = useState({sort:"",label:"random"});

  const ChangeHandler = (options) => {
    dispatc({ type: "filter", options });
    dispatc({ type: "priceFilter", options: priceStart });
    console.log(priceStart)
    setValue(options);
  };

  const priceChangeHandler = (options) => {
    dispatc({ type: "priceFilter", options });
    setPriceStart(options);
  };

  return (
    <div class="all">
      filter based on size and price:
      <div class="selStyle">
        <Select value={value} onChange={ChangeHandler} options={options} />
        <Select
          value={priceStart}
          onChange={priceChangeHandler}
          options={priceOptions}
        />
        <SearchBar sort={priceStart} />
      </div>
    </div>
  );
};

export default Filter;
