import React, { useContext, useReducer } from "react";
import { ProdactsData } from "../db/Products";
import _ from "lodash";
const ProductContext = React.createContext();
const ProductContextDispatcher = React.createContext();

const reducer = (state, Action) => {
  switch (Action.type) {
    case "remove": {
      console.log("removed");
      const filterdProducts = state.filter((p) => p.id !== Action.id);
      return filterdProducts;
    }

    case "increament": {
      console.log("increased");
      const index = state.findIndex((p) => p.id === Action.id);
      const newProduct = { ...state[index] };
      newProduct.quantity++;
      const newProducts = [...state];
      newProducts[index] = newProduct;
      return newProducts;
    }

    case "decreament": {
      const index = state.findIndex((p) => p.id === Action.id);
      const newProduct = { ...state[index] };
      if (newProduct.quantity === 1) {
        const filterdProducts = state.filter((p) => p.id !== Action.id);
        return filterdProducts;
      } else {
        newProduct.quantity--;
        const newProducts = [...state];
        newProducts[index] = newProduct;
        return newProducts;
      }
    }
    case "edite": {
      const index = state.findIndex((p) => p.id === Action.id);
      const newProduct = { ...state[index] };
      newProduct.Name = Action.e.target.value;
      const newProducts = [...state];
      newProducts[index] = newProduct;
      return newProducts;
    }
    case "filter": {
      if (Action.options.value === "") {
        return ProdactsData;
      } else {
        const updatedProducts = state.filter(
          (p) => p.avability.indexOf(Action.options.value) >= 0
        );
        return updatedProducts;
      }
    }
    case "priceFilter": {
      const value = Action.options.sort;
      if (value === "") {
        console.log(value);
        const products = [...state];
        return products;
      } else if (value === "Cheapest") {
        return _.orderBy([...state], ["price"], ["asc"]);
      } else if (value === "Most Expensive") {
        return _.orderBy([...state], ["price"], ["desc"]);
      }
    }
    case "search": {
      const searchKey = Action.value;
      const searchedProducts = state.filter((p) => p.Name.toLocaleLowerCase().includes(searchKey.toLocaleLowerCase()));
      console.log(searchedProducts);
      return searchedProducts;
    }
  }
};

const ProductProvider = ({ children }) => {
  const [products, Dispatcher] = useReducer(reducer, ProdactsData);
  return (
    <ProductContext.Provider value={products}>
      <ProductContextDispatcher.Provider value={Dispatcher}>
        {children}
      </ProductContextDispatcher.Provider>
    </ProductContext.Provider>
  );
};

export default ProductProvider;

export const useProducts = () => useContext(ProductContext);
export const useProductsDispatcher = () => useContext(ProductContextDispatcher);
export const useProductsActions = () => {
  return useContext(ProductContextDispatcher);
};
