import React from "react";
import Products from "./Products";
import {
  useProducts,
  useProductsActions,
} from "./prpductProvider/ProductProvider";

const ProductsList = () => {
  const products = useProducts();
  const dispatch = useProductsActions();
  const productsRender = () => {
    return products.map((product) => {
      return (
        <div key={product.id}>
          <Products
            change={(e) => dispatch({ type: "edite", id: product.id, e: e })}
            decresQuantity={() =>
              dispatch({ type: "decreament", id: product.id })
            }
            increaseQuantity={() =>
              dispatch({ type: "increament", id: product.id })
            }
            quantity={product.quantity}
            title={product.Name}
            price={product.price}
            update ={()=>dispatch({type:"remove",id:product.id})}
          />
        </div>
      );
    });
  };

  return productsRender();
};

export default ProductsList;
