import "./prod.css";
import { BiTrash } from "react-icons/bi";
const Counter = (props) => {
  return (
    <div class={"container"}>
      <p>
        {props.title} - {props.price}
      </p>
      <div>
        <button
          class={`${"quantity"} ${props.quantity === 1 && "remove"}`}
          onClick={props.decresQuantity}
        >
          {props.quantity === 1 ? <BiTrash /> : "-"}
        </button>
        <button class={"quantity"}>{props.quantity}</button>
        <button class={"incbtn"} onClick={props.increaseQuantity}>
          +
        </button>
        <button onClick={props.update}>delete</button>
      </div>
    </div>
  );
};

export default Counter;
