import React, { useState } from "react";
import { useProductsDispatcher } from "../../Components/prpductProvider/ProductProvider";
import "./Search.css";
const SearchBar = ({sort}) => {
  const dispatc = useProductsDispatcher();
  let value;
  const changeHadler = (e) => {
    value = e.target.value;
    dispatc({ type: "search", value });
    dispatc({type:"priceFilter",options: sort})
  };
  return (
    <dvi>
      <input
        type={"text"}
        onChange={changeHadler}
        value={value}
        placeholder={"Search for ..."}
      />
    </dvi>
  );
};

export default SearchBar;
