import React from "react";
import styles from "./Navbar.module.css";
import { BiBasket } from "react-icons/bi";
import { useProducts } from "./prpductProvider/ProductProvider";
const Navbar = () => {
  const products = useProducts();
  const items = products.filter((p)=>p.quantity>0).length;
  return (
    <header className={styles.navbar}>
      <h2>Mazi Shop</h2>
      <span><BiBasket class="BiBasket"/> {items}</span>
    </header>
  );
};

export default Navbar;
